//
// c3.scss
//

.c3-tooltip {
    box-shadow: $box-shadow-lg;
    opacity: 1;

    td {
        border-left: none;

        // font-family: $font-family-secondary;
        >span {
            background: var(--#{$prefix}dark);

        }
    }

    tr {
        border: none !important;
    }

    th {
        background-color: var(--#{$prefix}dark);
        color: var(--#{$prefix}tertiary-bg);
    }
}

.c3-chart-arcs-title {
    font-size: 18px;
    font-weight: 600;
}

.c3 {
    text {
        // font-family: $font-family-base;
        fill: var(--#{$prefix}light-text-emphasis);
    }

    line,
    path {
        stroke: var(--#{$prefix}dark-bg-subtle);
    }
}


.c3-chart-arc.c3-target {
    g {
        path {
            stroke: $white;
        }
    }
}