// maintenance.scss
//

.svg-computer {
  stroke-dasharray: 1134;
  stroke-dashoffset: -1134;
  animation: draw-me 5s infinite;
  animation-direction: normal;
  height: 200px;
}

@keyframes draw-me {
  from {
      stroke-dashoffset: -1134;
  }
  to {
      stroke-dashoffset: 0;
  }
}

@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .svg-computer {
      stroke-dasharray: 0;
      stroke-dashoffset: 0;
      animation: none;
      animation-direction: normal;
  }
}